import * as React from "react";
import { StaticImage } from "gatsby-plugin-image";

import { useCurrentYear } from "../hooks/useCurrentYear";

export interface FooterProps {
  owner: string;
}

export function Footer(props: FooterProps) {
  const currentYear = useCurrentYear();

  return (
    <footer className="text-white body-font">
      <div className="bg-black">
        <div className="container mx-auto pt-4 pb-6 px-5 sm:px-24 flex flex-wrap flex-col sm:flex-row items-center">
          <p className="text-sm text-center sm:text-left pb-4 sm:pb-0">
            {`© ${currentYear} ${props.owner}`}
          </p>
          <span className="inline-flex sm:ml-auto sm:mt-0 mt-2 justify-center sm:justify-start">
            <StaticImage
              src="../images/logo_sm_w.png"
              alt="Steinwohl"
              placeholder="blurred"
              layout="fixed"
              height={36}
            />
          </span>
        </div>
      </div>
    </footer>
  );
}
