import * as React from "react";

export interface CardGalleryProps {
  cards: Array<CardProps>;
}

export function CardGallery(props: CardGalleryProps) {
  return (
    <div className="bg-black text-white text-lg body-font mx-auto">
      <div className="grid grid-flow-row grid-cols-2 md:grid-cols-3">
        {props.cards.map((val, idx) => (
          <div className="pb-4 object-contain" key={idx}>
            <Card {...val} />
          </div>
        ))}
      </div>
    </div>
  );
}

interface CardProps {
  imgSrc: string;
  imgAlt?: string;
  title: string;
}

function Card(props: CardProps) {
  return (
    <div className="h-full flex flex-col items-center text-center">
      <img
        className="flex-shrink-0 w-full h-96 object-cover object-center mb-4"
        src={props.imgSrc}
        alt={props.imgAlt}
      />
      <div className="w-full">
        <h3 className="">{props.title}</h3>
      </div>
    </div>
  );
}
