import * as React from "react";
import { graphql, useStaticQuery } from "gatsby";

export interface IntroductionProps {}

export function Introduction(_props: IntroductionProps) {
  const data = useStaticQuery(graphql`
    query {
      file(name: { regex: "/^main$/" }) {
        publicURL
        name
      }
    }
  `);

  const imgSrc = data.file?.publicURL;

  return (
    <section className="text-gray-600 body-font" id="home">
      <div className="flex pb-12 sm:pb-24 md:flex-row flex-col items-center">
        <div className="flex-1">
          <img className="object-cover object-center" alt="hero" src={imgSrc} />
        </div>
        <div className="flex-1 text-center md:text-left">
          <div className="flex flex-col md:p-24 p-8 py-12 bg-black text-white">
            <h1 className="title-font sm:text-4xl text-3xl mb-8 font-medium">
              Herzlich Willkommen auf steinwohl.at!
            </h1>
            <p className="mb-8 leading-relaxed">
              Unser Team aus akademischen Restauratoren, Bildhauern und
              Steinmetzen erfasst sämtliche Tätigkeiten im Bereich der
              Steinrestaurierung. Unsere Leistungen finden Sie hier. Gerne
              beraten wir Sie und bieten Ihnen kompetente und transparente
              Lösungen an.
            </p>
          </div>
        </div>
      </div>
    </section>
  );
}
