import * as React from "react";
import { Link } from "gatsby";
import { StaticImage } from "gatsby-plugin-image";

export interface NavBarLink {
  id: string;
  label: string;
}

export interface NavBarProps {
  id: string;
  links: NavBarLink[];
}

export function NavBar(props: NavBarProps) {
  const [isExpanded, setIsExpanded] = React.useState(true);
  const toggleExpanded = React.useCallback(() => {
    setIsExpanded((prevState) => !prevState);
  }, []);
  const [activeId, setActiveId] = React.useState(props.links[0].id);

  // const onScrollRef = React.useRef(null);
  // React.useEffect(() => {
  //   onScrollRef.current = () => {
  //     const selectors = props.links.map(({ id }) => `#${id}`).join(", ");
  //     const nodes = document.querySelectorAll(selectors);
  //     const node = Array.from(nodes)
  //       .map((val) => ({
  //         id: val.id,
  //         offsetTop: (val as any).offsetTop,
  //       }))
  //       .filter(({ offsetTop }) => offsetTop <= window.scrollY)
  //       .pop();

  //     if (!node) {
  //       return;
  //     }

  //     if (node.id === activeId) {
  //       return;
  //     }

  //     setActiveId(node.id);
  //   };
  // }, [props.links]);
  // React.useEffect(() => {
  //   const listener = () => {
  //     if (onScrollRef.current) {
  //       onScrollRef.current();
  //     }
  //   };
  //   document.addEventListener("scroll", listener);
  //   return () => {
  //     document.removeEventListener("scroll", listener);
  //   };
  // }, []);

  return (
    <nav className="bg-white-800" id={props.id}>
      <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-8">
        <div className="relative flex items-center justify-between h-16">
          <div className="absolute inset-y-0 left-0 flex items-center sm:hidden">
            {/* <!-- Mobile menu button--> */}
            <button
              type="button"
              className="inline-flex items-center justify-center p-2 text-black hover:text-white hover:bg-gray-500 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white"
              aria-controls="mobile-menu"
              aria-expanded={isExpanded ? "true" : "false"}
              onClick={toggleExpanded}
            >
              <span className="sr-only">Open main menu</span>
              {/* <!--
                    Icon when menu is closed.
        
                    Heroicon name: outline/menu
        
                    Menu open: "hidden", Menu closed: "block"
                  --> */}
              <svg
                className="block h-6 w-6"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                aria-hidden="true"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
              {/* <!--
                    Icon when menu is open.
        
                    Heroicon name: outline/x
        
                    Menu open: "block", Menu closed: "hidden"
                  --> */}
              <svg
                className="hidden h-6 w-6"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                aria-hidden="true"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </button>
          </div>
          <div className="flex-1 flex items-center justify-center sm:items-stretch sm:justify-start">
            <div className="flex-shrink-0 flex items-center">
              <StaticImage
                className="block h-8 w-auto"
                src="../images/logo_md.png"
                alt="Steinwohl"
                placeholder="blurred"
                layout="fixed"
                height={36}
              />
            </div>
            <div className="hidden sm:block sm:ml-6">
              <div className="flex space-x-4">
                {/* <!-- Current: "bg-gray-900 text-white", Default: "text-gray-300 hover:bg-gray-700 hover:text-white" --> */}
                {props.links.map((val) => (
                  <NavBarLabelDesktop
                    isActive={val.id === activeId}
                    key={val.id}
                    id={val.id}
                    label={val.label}
                    onClick={() => {
                      setActiveId(val.id);
                    }}
                  />
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* <!-- Mobile menu, show/hide based on menu state. --> */}
      {isExpanded && (
        <div className="sm:hidden" id="mobile-menu">
          <div className="">
            {props.links.map((val) => (
              <NavBarLabelMobile
                isActive={val.id === activeId}
                key={val.id}
                id={val.id}
                label={val.label}
                onClick={() => {
                  setActiveId(val.id);
                }}
              />
            ))}
          </div>
        </div>
      )}
    </nav>
  );
}

/**
 *
 */
interface NavBarLabelDesktopProps {
  id: string;
  isActive?: boolean;
  label: string;
  onClick: React.MouseEventHandler<HTMLAnchorElement>;
}

const NavBarLabelDesktop = React.forwardRef<any, NavBarLabelDesktopProps>(
  (props, ref) => {
    const classesCommon = "px-3 py-2 text-sm font-medium";
    const classesActive = "bg-black text-white";
    const classesInactive = "text-black hover:bg-gray-500 hover:text-white";
    const className = React.useMemo(
      () =>
        [props.isActive ? classesActive : classesInactive, classesCommon].join(
          " "
        ),
      [props.isActive]
    );

    return (
      <Link
        aria-current={props.isActive ? "page" : undefined}
        className={className}
        onClick={props.onClick}
        ref={ref}
        to={`#${props.id}`}
      >
        {props.label}
      </Link>
    );
  }
);

/**
 *
 */
interface NavBarLabelMobileProps {
  id: string;
  isActive?: boolean;
  label: string;
  onClick: React.MouseEventHandler<HTMLAnchorElement>;
}

const NavBarLabelMobile = React.forwardRef<any, NavBarLabelMobileProps>(
  (props, ref) => {
    const classesCommon = "block px-3 py-2 text-base font-medium text-center";
    const classesActive = "bg-black text-white";
    const classesInactive = "text-black hover:bg-gray-500 hover:text-white";
    const className = React.useMemo(
      () =>
        [props.isActive ? classesActive : classesInactive, classesCommon].join(
          " "
        ),
      [props.isActive]
    );

    return (
      <Link
        aria-current={props.isActive ? "page" : undefined}
        className={className}
        onClick={props.onClick}
        ref={ref}
        to={`#${props.id}`}
      >
        {props.label}
      </Link>
    );
  }
);

