import * as React from "react";
import { graphql, useStaticQuery } from "gatsby";

import { CardGallery, CardGalleryProps, Title } from "../components";

const services: CardGalleryProps["cards"] = [
  {
    imgSrc: "services1",
    imgAlt: "Untersuchungen",
    title: "Untersuchungen",
  },
  {
    imgSrc: "services4",
    imgAlt: "Skulptur",
    title: "Skulptur",
  },
  {
    imgSrc: "services3",
    imgAlt: "Objekthandling",
    title: "Objekthandling",
  },
  {
    imgSrc: "services2",
    imgAlt: "Pflegekonzept",
    title: "Pflegekonzept",
  },
  {
    imgSrc: "services5",
    imgAlt: "Fassade",
    title: "Fassade",
  },
  {
    imgSrc: "services6",
    imgAlt: "Rekonstruktion",
    title: "Rekonstruktion",
  },
];

export interface ServicesProps {}

export function Services(_props: ServicesProps) {
  const data = useStaticQuery(graphql`
    query {
      allFile(filter: { name: { regex: "/^services\\d/" } }) {
        nodes {
          name
          publicURL
        }
      }
    }
  `);

  const cards = React.useMemo(
    () =>
      services
        .map((service) => {
          const image = data.allFile.nodes.find(
            (node) => node.name === service.imgSrc
          );
          return !image
            ? null
            : {
                ...service,
                imgSrc: image.publicURL,
              };
        })
        .filter(Boolean),
    [data]
  );

  return (
    // <Section id="services" title="Leistungen">
    //   <CardGallery cards={cards} />
    // </Section>
    <section id="services">
      <Title title="Leistungen" />
      <div className="pb-12 sm:pb-24">
        <CardGallery cards={cards} />
      </div>
    </section>
  );
}
